.thanks {
  display: flex;
  justify-content: flex-end;
  color: $color-titles;
  padding: 4em 1em 1em 1em;
  @include from('xl') { padding-top: 1em;  }

  svg {
    @include resize-aspect-ratio(10rem);
    transform: rotate(30deg);
    path { fill: $color-logos-main; }

    margin: -4rem 0rem -2rem 2rem;

    @include up-to('sm') {
      margin-left: 0;
    }
    @include from('xl') { margin-top: -6rem; }
  }
}