#Header {
  @include up-to('md') {
    font-size: 12px;
  }

  .field__subject, .field__occupation {
    width: 70%;
  }

  .field__subject {
    @include up-to('xs') {
      width: 60%;
    }
  }
  .field__occupation {
    @include up-to-custom-screen(470px) {
      width: 100%;
    }
  }

  .field__account {
   display: inline-flex;
   align-items: baseline;
   @include two-column-content(7.5em);

   .field__text {
     word-wrap: break-word;
   }
  }

 .field .field__name .logo {
    display: inline-block;
    vertical-align: middle;
    svg {
      @include resize-aspect-ratio(1rem);
      margin-right: 0.5em;
      path {
        @include theme-aware('fill', 'color-logos-main', $themes);
      }
    }
  }
}